.controls {
  position: relative; }

.controls--suggestion-area {
  cursor: pointer;
  width: 196px;
  margin-left: 20px;
  margin-top: -5px;
  max-height: 100px;
  background-color: #ffffff;
  position: absolute;
  overflow: scroll;
  opacity: .9;
  z-index: 1; }

.controls--suggestion {
  display: block;
  height: 20px; }
  .controls--suggestion:hover {
